"use client"

import { useState } from "react"
import UploadPanel from "@/components/UploadPanel"
import GraphView from "@/components/GraphView"
import RingTable from "@/components/RingTable"
import RiskAnalytics from "@/components/RiskAnalytics"
import { exportReport } from "@/lib/exportReport"
import { ForensicResponse } from "@/types/forensic"

export default function Home() {
  const [data, setData] = useState<ForensicResponse | null>(null)
  const [selectedAccount, setSelectedAccount] = useState<string | null>(null)
  const [selectedRing, setSelectedRing] = useState<string | null>(null)
  const [searchQuery, setSearchQuery] = useState("")
  const [suspiciousOnly, setSuspiciousOnly] = useState(false)

  return (
    <main className="min-h-screen bg-[#0f172a] text-gray-200">
      <div className="max-w-[1400px] mx-auto px-6 py-16">

        {/* ================= PRE-UPLOAD ================= */}
        {!data && (
          <div className="flex flex-col items-center justify-center min-h-[70vh] space-y-12 text-center">
            <h1 className="text-7xl font-black tracking-widest text-white">
              GOLMAAL
            </h1>

            <UploadPanel onData={setData} centered />
          </div>
        )}

        {/* ================= POST-UPLOAD ================= */}
        {data && (
          <div className="space-y-16">

            {/* HEADER */}
            <div className="text-center">
              <h1 className="text-5xl font-black tracking-widest text-white">
                GOLMAAL
              </h1>
            </div>

            {/* MAIN WORKSPACE */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-10">

              {/* LEFT: GRAPH */}
              <div className="bg-[#1e293b] rounded-2xl border border-slate-700 shadow-lg p-4">
                <GraphView
                  data={data}
                  suspiciousOnly={suspiciousOnly}
                  highlightedAccount={selectedAccount}
                  selectedRing={selectedRing}
                  onNodeClick={(id) => setSelectedAccount(id)}
                />
              </div>

              {/* RIGHT: CONSOLE */}
              <div className="space-y-6">

                <div className="bg-[#1e293b] rounded-2xl p-6 border border-slate-700">
                  <UploadPanel onData={setData} />
                </div>

                <div className="bg-[#1e293b] rounded-2xl p-6 border border-slate-700 space-y-4">
                  <input
                    type="text"
                    placeholder="Search account..."
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    className="w-full bg-[#0f172a] border border-slate-600 px-4 py-3 rounded-lg text-gray-200"
                  />

                  <button
                    onClick={() =>
                      setSelectedAccount(searchQuery.trim().toUpperCase())
                    }
                    className="w-full bg-blue-600 hover:bg-blue-700 py-2 rounded-lg"
                  >
                    Search
                  </button>

                  <label className="flex items-center gap-2 text-sm text-gray-400">
                    <input
                      type="checkbox"
                      checked={suspiciousOnly}
                      onChange={() =>
                        setSuspiciousOnly(!suspiciousOnly)
                      }
                    />
                    Show Suspicious Only
                  </label>
                </div>
              </div>
            </div>

            {/* ANALYTICS */}
            <RiskAnalytics data={data} />

            {/* DOWNLOAD */}
            <div className="flex justify-center">
              <button
                onClick={() => exportReport(data)}
                className="bg-emerald-600 hover:bg-emerald-700 px-8 py-3 rounded-xl font-semibold shadow-lg"
              >
                Download Full JSON Report
              </button>
            </div>

            {/* RING TABLE */}
            <RingTable
              rings={data.fraud_rings}
              selectedRing={selectedRing}
              onSelectRing={setSelectedRing}
            />
          </div>
        )}
      </div>
    </main>
  )
}
